      window.kakao && window.kakao.maps ? initMap() : addKakaoMapScript();

      const btn = document.querySelector(".sms-share-btn");
      btn.addEventListener("click", () => {
        var mobileType = checkMobile();
        checkM(mobileType);
      });

      // screen size
      function setScreenSize() {
        let vh = window.innerHeight * 0.01;

        document.documentElement.style.setProperty("--vh", `${vh}px`);
      }
      setScreenSize();
      window.addEventListener("resize", () => {
        setScreenSize();
      });

      // scroll animation
      window.addEventListener("scroll", function () {
        var sections = document.querySelectorAll(".reveal");
        for (var i = 0; i < sections.length; i++) {
          var windowHeight = window.innerHeight;
          var elementTop = sections[i].getBoundingClientRect().top;
          var elementVisible = 150;

          if (elementTop < windowHeight - elementVisible) {
            sections[i].classList.add("active");
          } else {
            sections[i].classList.remove("active");
          }
        }
      });

      ddayInterval = setInterval(() => {
        dday.value = getDday();
      }, 1000);
    });

    // kakao
    function addKakaoMapScript() {
      const script = document.createElement("script");
      /* global kakao */
      script.onload = () =>
        kakao.maps.load(() => {
          initMap();
        });

      script.src = `//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=${process.env.VUE_APP_KAKAO_JAVASCRIPT_KEY}`;
      document.head.appendChild(script);
    }

    function initMap() {
      var container = document.getElementById("map");
      var options = {
        center: new kakao.maps.LatLng(35.883487491158675, 128.65892377049653),
        level: 5,
      };

      var map = new kakao.maps.Map(container, options);

      for (var index = 0; index < markerInfos.length; index++) {
        var imageSrc = require(`@/assets/${markerInfos[index].img}`);
        // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄÏùò Ïù¥ÎØ∏ÏßÄ ÌÅ¨Í∏∞ ÏûÖÎãàÎã§
        var imageSize = new kakao.maps.Size(
          markerInfos[index].size,
          markerInfos[index].size
        );

        // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄÎ•º ÏÉùÏÑ±Ìï©ÎãàÎã§
        var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize);

        // ÎßàÏª§Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
        var marker = new kakao.maps.Marker({
          map: map, // ÎßàÏª§Î•º ÌëúÏãúÌï† ÏßÄÎèÑ
          position: new kakao.maps.LatLng(
            markerInfos[index].lat,
            markerInfos[index].lng
          ), // ÎßàÏª§Î•º ÌëúÏãúÌï† ÏúÑÏπò
          title: markerInfos[index].title, // ÎßàÏª§Ïùò ÌÉÄÏù¥ÌãÄ, ÎßàÏª§Ïóê ÎßàÏö∞Ïä§Î•º Ïò¨Î¶¨Î©¥ ÌÉÄÏù¥ÌãÄÏù¥ ÌëúÏãúÎê©ÎãàÎã§
          image: markerImage, // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄ
        });

        marker.setMap(map);
      }
    }

    // Ïπ¥Ïπ¥Ïò§ Í≥µÏú†
    function shareKakao() {
      window.Kakao.Share.sendDefault({
        objectType: "location",
        address: "ÎåÄÍµ¨ ÎèôÍµ¨ ÎèôÏ¥åÎ°ú 200",
        addressTitle: "ÌÄ∏Î≤®Ìò∏ÌÖî 1Ï∏µ ÌÖåÎùºÌïòÏö∞Ïä§ÌôÄ",
        content: {
          title: "ÍπÄÏ§ÄÏö∞ü§çÏßÑÏÜåÏ†ï Í≤∞ÌòºÏãùÏóê Ï¥àÎåÄÌï©ÎãàÎã§.",
          description:
            "2022ÎÖÑ 09Ïõî 24Ïùº ÌÜ†ÏöîÏùº Ïò§ÌõÑ 1Ïãú ÌÄ∏Î≤®Ìò∏ÌÖî 1Ï∏µ ÌÖåÎùºÌïòÏö∞Ïä§ÌôÄ",
          imageUrl: "https://w1031oo.github.io/kakaoShare.jpg",
          link: {
            mobileWebUrl: url,
            webUrl: url,
          },
        },
        // social: {
        //   likeCount: 286,
        //   commentCount: 45,
        //   sharedCount: 845,
        // },
        buttons: [
          {
            title: "Ï≤≠Ï≤©Ïû• Î≥¥Í∏∞",
            link: {
              mobileWebUrl: url,
              webUrl: url,
            },
          },
        ],
      });
    }

    // Î¨∏Ïûê Í≥µÏú†ÌïòÍ∏∞

    function checkM(m) {
      location.href =
        "sms:" +
        "" +
        (m == "ios" ? "&" : "?") +
        "body=" +
        encodeURIComponent("ÍπÄÏ§ÄÏö∞ü§çÏßÑÏÜåÏ†ï Í≤∞ÌòºÏãùÏóê Ï¥àÎåÄÌï©ÎãàÎã§.\n" + url);
    }

    function checkMobile() {
      var varUA = navigator.userAgent.toLowerCase(); //userAgent Í∞í ÏñªÍ∏∞
      if (varUA.indexOf("android") > -1) {
        //ÏïàÎìúÎ°úÏù¥Îìú
        return "android";
      } else if (
        varUA.indexOf("iphone") > -1 ||
        varUA.indexOf("ipad") > -1 ||
        varUA.indexOf("ipod") > -1
      ) {
        //IOS
        return "ios";
      } else {
        //ÏïÑÏù¥Ìè∞, ÏïàÎìúÎ°úÏù¥Îìú Ïô∏
        return "other";
      }
    }

    // check mobile and kakao pay
    function sendKakaoPay(url) {
      var isMobile =
        /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          navigator.userAgent
        );

      if (isMobile) {
        location.href = url;
      } else {
        window.alert("Ïπ¥Ïπ¥Ïò§ÌéòÏù¥Îäî Î™®Î∞îÏùºÏóêÏÑú ÏÇ¨Ïö©Ìï† Ïàò ÏûàÏäµÎãàÎã§.");
      }
    }

    // copy
    function copy(text) {
      copyText(text, undefined, (error, event) => {
        if (error) {
          alert("Can not copy");
          console.log(error);
        } else {
          alert("Î≥µÏÇ¨ÎêòÏóàÏäµÎãàÎã§.");
          console.log(event);
        }
      });
    }

    // d-day Í≥ÑÏÇ∞
    function getDday() {
      // var masTime = new Date("2022-09-24");
      const masTime = new Date("2022-09-24T13:00:00+0900");
      const todayTime = new Date();

      const diff = masTime - todayTime;

      const diffDay = Math.floor(diff / (1000 * 60 * 60 * 24));
      const diffHours = Math.floor((diff / (1000 * 60 * 60)) % 24);
      const diffMinutes = Math.floor((diff / (1000 * 60)) % 60);
      const diffSeconds = Math.floor((diff % (1000 * 60)) / 1000);

      if (diff > 0) {
        var day = diffDay > 0 ? `${diffDay}Ïùº` : "";
        var hours = diffHours > 0 ? `${diffHours}ÏãúÍ∞Ñ` : "";
        var minutes = diffMinutes > 0 ? `${diffMinutes}Î∂Ñ` : "";
        var seconds = diffSeconds > 0 ? `${diffSeconds}Ï¥à` : "";

        console.log("interval");

        return `${day != "" ? `${day} ` : ""}
      ${hours != "" ? `${hours} ` : ""}
      ${minutes != "" ? `${minutes} ` : ""}
      ${seconds != "" ? `${seconds}` : ""}`;
      } else {
        if (ddayInterval) {
          clearInterval(ddayInterval);
        }
        return `Ï∏°ÌïòÌï¥ Ï£ºÏã† Î™®Îì† Î∂ÑÎì§ÏóêÍ≤å Í∞êÏÇ¨ÎìúÎ¶ΩÎãàÎã§.`;
      }
    }

    // const attrs = {
    //   key: "today",
    //   dates: new Date(),
    //   highlight: true,
    // };
    return {
      parents,
      test,
      imageIndex,
      images,
      junAccountInfo,
      ssoAccountInfo,
      shareKakao,
      sendKakaoPay,
      copy,
      getDday,
      dday,
    };
  },
};
</script>

